import * as types from '../constants/ActionTypes'

import * as parametres from '../constants/parametres'

const initialState = {
	name:'',
	image:'',
	history:[parametres.START]
}



export default function user (state = initialState, action) {
	switch (action.type) {
		case types.PREVIOUS:
			var new_history=[...state.history]
			new_history.pop()
			state={...state,history:new_history}
			//state.history.pop();
			return state
		case types.GOOGLE_LOGIN_SUCCESS:
			state={
				name:action.user.name,
				image:action.user.image,
				history:action.user.history
			}
			return state
		case types.GET_INFOS_SUCCESS:
			if(action.user.login)
			{
			state={
				name:action.user.name,
				image:action.user.image,
				history:action.user.history
			}
			}
			return state
		case types.LOGOUT_SUCCESS:
			state={
				name:'',
				image:'',
				history:[parametres.START]
			}
			return state
		case types.NEXT:
			var new_history=state.history
			new_history.push(action.id_next_page)
			state={
				...state,
				history:new_history
			}
			return state
		case types.DELETE_ACCOUNT_SUCCESS:
			state={
				name:'',
				image:'',
				history:[parametres.START]
			}
			return state
		default:
			return state
	}
}
