import { connect } from 'react-redux'

import Save from '../components/Save'

//import { fetchGetInfos, executeCard, fetchNewMessages, fetch_on_line, fetch_off_line} from '../actions'

import { action_login, action_logout, action_delete_account } from '../actions'

const mapStateToProps = (state) => ({
	user_name:state.user.name,
	user_image:state.user.image
})

const SaveConnect = connect(
	mapStateToProps,
	{ action_login, action_logout, action_delete_account }
)(Save)

export default SaveConnect
