import React, { Component } from 'react'

import logo from './logo.svg';
import './App.css';

import Page from './containers/Page'
import Save from './containers/Save'
import Footer from './components/Footer'
import Loading from './components/Loading'
import Pace from './containers/Pace'
import Start from './components/Start'

import 'bootstrap/dist/css/bootstrap.min.css';

export default class App extends Component {

  constructor(props) {
    super(props);
    this.props.action_getInfos()
    var mode='normal'
    var pathname=window.location.pathname
		if(pathname=='/start') mode='start'
    this.state = {
                   mode: mode
                 }
  }

  toNormal = () => {
		this.setState({
			mode:'normal'
    })
	}

  render () {
  //props.action_getInfos()
  var pages_length=this.props.pages.length
  return (
    <div className="App">
      <Pace/>
      {pages_length==0 ? (
      <Loading/>
    ) : (
      <div>
        <Save/>
        {this.state.mode=='normal' ? <Page/> : <Start toNormal={this.toNormal}/>}
      </div>
    )}
      <Footer/>
    </div>
  );
}
}
