import React, { Component } from 'react'
import './Interactive.css';
//import PropTypes from 'prop-types'
//import classnames from 'classnames'

//import TodoTextInput from '../TodoTextInput'

export default class Interactive extends Component {

	render () {

		return (
			<div className='interactive'>
				<img src={require('../start.png')} id='image'/>
				{this.props.interventions.map((intervention) => {
					return <div className='clickable' style={{width:intervention.width+'%',height:intervention.height+'%',top:intervention.top+'%',left:intervention.left+'%'}}></div>
				})}
			</div>
		)
	}
}
