function Save(props) {
		/*<div id='save'>
			<FcGoogle style={{verticalAlign: '-10%'}}/> Sauvegarder ma progression
		</div>*/
		const login = useGoogleLogin({
  		onSuccess: tokenResponse => props.action_login(tokenResponse.access_token),
		});

		const [modal, setModal] = useState(false);

		const toggle = () => setModal(!modal);

		const preDeleteUser = () => {

		  var confirmationOfDeletingService = prompt('Cette action est irrversible.\nEntrez "Supprimer" pour confirmer que vous souhaitez supprimer votre compte dfinitivement.', "");
		  if (confirmationOfDeletingService=='Supprimer') {
		    props.action_delete_account()
		  }
		}

		return (
			<div>
			{props.user_name=='' ? (
				<div id='save' onClick={() => login()}>
					<FcGoogle style={{verticalAlign: '-10%'}}/> Sauvegarder ma progression
				</div>
			) : (
			<Dropdown id='save_dropdown'>
      	<Dropdown.Toggle id="save_toggle">
					<img src={props.user_image} id='img_profil' crossOrigin='anonymous' referrerPolicy="no-referrer"/> {props.user_name} - Sauvegarde
      	</Dropdown.Toggle>
      	<Dropdown.Menu>
					<Dropdown.Item onClick={toggle}>Compte</Dropdown.Item>
					<Modal show={modal} onHide={toggle}>
		        <Modal.Header closeButton>Compte</Modal.Header>
		        <Modal.Body>
		          <span id='deleteService' onClick={preDeleteUser}>Supprimer mon compte</span>
		        </Modal.Body>
		        <Modal.Footer>
		          <Button className='btnFooter' onClick={toggle}>Revenir</Button>
		        </Modal.Footer>
		      </Modal>
        	<Dropdown.Item onClick={props.action_logout}>Se dconnecter</Dropdown.Item>
      	</Dropdown.Menu>
    	</Dropdown>
			)
			}
			</div>
		)
}

export default Save
