import React, { Component } from 'react'
import './Page.css';
//import PropTypes from 'prop-types'
//import classnames from 'classnames'

//import TodoTextInput from '../TodoTextInput'

import Carousel from 'nuka-carousel';

import PrevButton from '../containers/PrevButton'
import NextButton from '../containers/NextButton'

import Interactive from './Interactive'
import Tonext from './Tonext'

import * as parametres from '../constants/parametres'

export default class Page extends Component {

	/*
	state = {
		editing: false,
	}

	handleDoubleClick = () => {
		this.setState({ editing: true })
	}

	handleSave = (id, text) => {
		if (text.length === 0) {
			this.props.deleteTodo(id)
		} else {
			this.props.editTodo(id, text)
		}
		this.setState({ editing: false })
	}
	*/
	fill_carousel_data = (start_from,carousel_data) => {
		if(typeof start_from === 'undefined') return carousel_data;
		else{
			//search the next
			var pages=this.props.pages
			var next_page=pages.find(x => x.id === start_from);
			var next_page_id=next_page.next;
			//add the next
			carousel_data.push(next_page);
			//next
			return this.fill_carousel_data(next_page_id,carousel_data)
		}
	}

	render () {

		var pages=this.props.pages,
				history=this.props.user.history,
				last_index_history=history.length-1,
				carousel_data=[],
				history_filled=[];

		/*
		if(last_index_history!=-1)
		{
			start_from=history[last_index_history]
		}
		else
		{
			start_from=parametres.START
		}
		*/
		for(var i=0;i<history.length;i++)
		{
			var page=pages.find(x => x.id === history[i]);
			history_filled.push(page)
		}

		var start_from=history_filled[last_index_history].next

		carousel_data=this.fill_carousel_data(start_from,carousel_data)

		//var id_next_page=carousel_data[0].id

		carousel_data=[...history_filled,...carousel_data]

		//if start
		var pathname=window.location.pathname

		/*
		if(pathname=='/start')
		{
			carousel_data=[...history_filled,
				{
					type:2,
					img:require('../start.png'),
					interventions:[
						{
							width:30,
							height:12,
							top:83,
							left:35,
							next:1
						},
						{
							width:30,
							height:12,
							top:83,
							left:65,
							next:3
						}
					]
				}
			]
			last_index_history++
		}
		*/

		return (
			<div>
				<Carousel
				slideIndex={last_index_history}
				defaultControlsConfig={{
					pagingDotsStyle: {
    				display: 'none'
  				}
				}}
				renderCenterLeftControls={({ previousDisabled, previousSlide }) => (
					<PrevButton previousSlide={previousSlide} previousDisabled={previousDisabled}/>
  			)}
				renderCenterRightControls={({ nextDisabled, nextSlide }) => (
					<NextButton nextSlide={nextSlide} nextDisabled={nextDisabled} id_next_page={start_from}/>
  			)}
			  >
					{carousel_data.map((page) => {
						return page.type==1 ? (
							<img src={parametres.PREFIX_URL_PAGES+page.img} id='image' key={page.id}/>
						) : page.type==2 ? (
							<Interactive key={page.id} interventions={page.interventions}/>
						) : page.type==3 ? (
							<Tonext key={page.id}/>
						) : ''
					})}
				</Carousel>
			</div>
		)
	}
}
