import React, { useState } from 'react';
import './Footer.css';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

import ConditionsUtilisation from './ConditionsUtilisation/ConditionsUtilisation'
import MentionsLegales from './MentionsLegales/MentionsLegales'
//import PropTypes from 'prop-types'
//import classnames from 'classnames'

//import TodoTextInput from '../TodoTextInput'

//import Carousel from 'nuka-carousel';
//import { GrFormNext } from "react-icons/gr";

	const Footer = (props) => {

  var display_cgu=false

	var pathname=window.location.pathname
	if(pathname=='/cgu') display_cgu=true

	const [modal, setModal] = useState(display_cgu);

	const toggle = () => setModal(!modal);

	const [modalMentions, setModalMentions] = useState(false);

	const toggleMentions = () => setModalMentions(!modalMentions);

		return (

			<div id='footer'>
				<span onClick={toggle} className='credit'>CGU</span> | <span onClick={toggleMentions} className='credit'>Mentions lgales</span>
				<Modal show={modal} onHide={toggle} className='modal-xl'>
	        <Modal.Header closeButton>Rgles de confidentialit et conditions d'utilisation</Modal.Header>
	        <Modal.Body>
	          <ConditionsUtilisation />
	        </Modal.Body>
	        <Modal.Footer>
	          <Button className='btnFooter' onClick={toggle}>Revenir</Button>
	        </Modal.Footer>
	      </Modal>
	      <Modal show={modalMentions} onHide={toggleMentions}>
	        <Modal.Header closeButton>Mentions lgales</Modal.Header>
	        <Modal.Body>
	          <MentionsLegales />
	        </Modal.Body>
	        <Modal.Footer>
	          <Button className='btnFooter' onClick={toggleMentions}>Revenir</Button>
	        </Modal.Footer>
	      </Modal>
			</div>
		)
}

export default Footer;
