import * as types from '../constants/ActionTypes'
import * as APIConfig from '../constants/APIConfig'

export const previous = () => ({
	type: types.PREVIOUS
})

export const action_previous = () => {
	return (dispatch,getState) => {
		dispatch(previous())
		var history=getState().user.history
		var user_name=getState().user.name
		if(user_name!='')
		{
		return fetch(
			`/api/auth/update_history/`,
			{
				method: 'PUT',
				body: JSON.stringify({
					history:history
				}),
				headers: APIConfig.HEADERS,
			},
		)
		}
	}
}

export const next = (id_next_page) => ({
	type: types.NEXT,
	id_next_page:id_next_page
})

export const action_next = (id_next_page) => {
	return (dispatch,getState) => {
		dispatch(next(id_next_page))
		var history=getState().user.history
		var user_name=getState().user.name
		if(user_name!='')
		{
		return fetch(
			`/api/auth/update_history/`,
			{
				method: 'PUT',
				body: JSON.stringify({
					history:history
				}),
				headers: APIConfig.HEADERS,
			},
		)
		}
	}
}

// GOOGLE_LOGIN

export const googleLogin = () => ({
    type: types.GOOGLE_LOGIN
})
export const googleLoginSuccess = (user) => ({
    type: types.GOOGLE_LOGIN_SUCCESS,
    user
})


export const action_login = (accessToken) => {
	return (dispatch,getState) => {

		dispatch(googleLogin())

		var history=getState().user.history
		var body={access_token: accessToken}
		if(history.length>1) body.history=history

		var tokenBlob= new Blob([JSON.stringify(body, null, 2)], {type : 'application/json'});

		return fetch(
			//`${APIConfig.API_URI}/auth/fb`,
      `/api/auth/google/`,
			{
				method: 'POST',
				body: tokenBlob,
				headers: {
          ...APIConfig.HEADERS,
          //'X-CSRF-Token': csrfToken
        }
			},
		)
    .then((response) => {
      if (!response.ok) {
        throw new Error('Error - 404 Not Found')
      }
      return response.json()
    })
    .then((response) => {
			dispatch(googleLoginSuccess(response))

      //
    })
	}
}

//GET INFOS
export const getInfosSuccess = (user,pages) => ({
    type: types.GET_INFOS_SUCCESS,
    user: user,
    pages: pages
})

export const action_getInfos  = () => {
  return (dispatch) => {
    return fetch(
			`/api/auth/get_infos/`,
			{
				method: 'GET',
				headers: APIConfig.HEADERS
			},
		)
    .then((response) => {
      if (!response.ok) {
        throw new Error('Error - 404 Not Found')
      }
      return response.json()
    })
    .then((response) => {
				dispatch(getInfosSuccess(response.user,response.pages))
    })
	}
}

