import React, { Component } from 'react'
import './Start.css';
//import PropTypes from 'prop-types'
//import classnames from 'classnames'

//import TodoTextInput from '../TodoTextInput'

import Carousel from 'nuka-carousel';

import { GrFormNext } from "react-icons/gr";
import { GrFormPrevious } from "react-icons/gr";

import * as parametres from '../constants/parametres'

export default class Start extends Component {

	constructor(props) {
		super(props)
		this.state = {
									 step: 0,
									 where:0
								 }
	}

	toWelcome = () => {
		this.setState({
      step: 1,
			where:1
    })
	}

	toBye = () => {
		this.setState({
      step: 2,
			where:1
    })
	}

	toStart = () => {
		this.setState({
			where:0
    })
	}

	render () {

		var toStart_var=this.toStart

		return (
			<div>
				<Carousel
				slideIndex={this.state.where}
				defaultControlsConfig={{
					pagingDotsStyle: {
    				display: 'none'
  				}
				}}
				renderCenterLeftControls={({ previousDisabled, previousSlide }) => (
					<div>
					{
					  !previousDisabled
					  &&
					<button onClick={()=>{
						previousSlide()
						toStart_var()
					}} disabled={previousDisabled} id='button'>
						<GrFormPrevious id='prev'/>
					</button>
					}
					</div>
  			)}
				renderCenterRightControls={({ nextDisabled, nextSlide }) => (
					<div>
					{
					  !nextDisabled
					  &&
					<button onClick={nextSlide} disabled={nextDisabled} id='button'>
						<GrFormNext id='prev'/>
					</button>
					}
					</div>
  			)}
			  >
					<div className='interactive'>
						<img src={parametres.PREFIX_URL_SCENES+'start1.png'} id='image'/>
						<div className='clickable' style={{width:'30%',height:'12%',top:'83%',left:'35%'}} onClick={this.toWelcome}></div>
						<div className='clickable' style={{width:'30%',height:'12%',top:'83%',left:'65%'}} onClick={this.toBye}></div>
					</div>
					{ this.state.step==1 ? (
						<div className='interactive'>
							<img src={parametres.PREFIX_URL_SCENES+'start2.png'} id='image'/>
							<div className='containerNext'>
								<button id='button' className='to_history' onClick={this.props.toNormal}>
									Continuer vers l'histoire
								</button>
							</div>
						</div>
					) : this.state.step==2 ? (
						<div className='interactive'>
							<img src={parametres.PREFIX_URL_SCENES+'start3.png'} id='image'/>
							<div className='containerNext'>
								<button id='button' className='to_history' onClick={this.props.toNormal}>
									Continuer vers l'histoire
								</button>
							</div>
						</div>
					) : ''}
				</Carousel>
			</div>
		)
	}
}
